import React, {Component} from 'react';

class Bio extends Component{
    render(){
        return(
            <div className="bio">
                <div className="photo-container">
                    <img src={this.props.photo} alt="Miranda" className="miranda-photo"></img>
                </div> 
                <div className="bio-container">
                    Fetter grne, du Laub,
                    Am Rebengelnder
                    Hier[mein Fenster herauf!] 1
                    Gedrngter quellet,
                    Zwillingsbeeren, und reifet
                    Schneller und glnzend[voller] 2!
                        Euch brtet der Mutter Sonne
                    Scheideblick, euch umsuselt
                    Des holden Himmels
                    Fruchtende Flle;
                    Euch khlet des Mondes
                    Freundlicher Zauberhauch,
                    Und euch betauen, ach!
                        Aus diesen Augen
                    Der ewig belebenden Liebe
                    Voll schwellende Trnen.
                </div>
            </div>
        );
    }
}

export default Bio;


